.head-ido {
    position: fixed;
    width: 100% !important;
    padding-top: 1rem;
    padding-bottom: 1.2rem;
    padding-left: 2.2rem;
    padding-right: 1rem;
    z-index: 150;
    background: linear-gradient(180deg, #746969, rgba(82, 77, 77, 0.5));
  }
  

  .logoDesc-ido {
    align-self: flex-start;
    font-size: 1.5rem;
    font-weight: bold;
  }

  .rotatedIDO {
    color: rgb(43, 204, 142) !important;
    font-size: 19px;
    font-family: monospace;
  }

  .address-ido {
    background-color: #10252049 !important;
    box-shadow: 1px 3px 5px 3px rgba(25, 46, 60, 2);
    .MuiButton-startIcon { 
        margin: 0;
    }
  }
  .address-ido:hover {
    box-shadow: none !important;
  }
  .address-ido-startIcon { 
    background-color: inherit; 
    height: 20px;
  }

  
  @media only screen and (max-width: 800px) {

    .language { 
        padding: 0 !important;
        width: 100% !important;
        .selectLanguageBtn { 
            width: 100% !important;
            justify-content: center !important;
        }
    }


  }

  @media only screen and (max-width: 550px) { 

    .head-ido {
        padding: 1rem !important;
      }

      .logoDesc-ido {
        font-size: 1rem !important;
      }

  }
