.gloableIDO {
  width: 100vw;
  // background: no-repeat center url(../../assets/images/bg.png);
  background: no-repeat center url(../../assets/images/pg_dark.png);
  background-size: 100% 100%;
  color: rgb(254, 254, 254);
  // filter: contrast(110%);
  background-attachment: fixed;
  cursor: url(../../assets/images/mouse.png), auto;
  
}
.containIDO {
  width: 100%;
  padding-top: 6.5rem;
  padding-bottom: 3rem;
  padding-left: 4rem;
  padding-right: 4rem;
}

.taglib { 
  padding-top: 2rem;
  padding-bottom: 1rem;
}


.baseFontFamily {
  font-family: "Exotc350-Bd-BT-Bold" !important;
  color: #4f13a9 !important;
  .MuiOutlinedInput-notchedOutline {
    border-color: #a659ff !important;
  }
}
.baseFontFamilyACreed {
  font-family: "Exotc350-Bd-BT-Bold" !important;
  color: #FCFCFC !important;
  .MuiOutlinedInput-notchedOutline {
    border-color: #2c2628 !important;
  }
}

.IDOBtn {
  color: rgb(96, 219, 204) !important;
  border: 3px solid rgb(38, 99, 80) !important;
  cursor: url(../../assets/images/mouseSelect.png) 14 0, pointer !important;
}
.BurnACreedBtn { 
  color: #FCFCFC !important;
  border: 3px solid #2c2628 !important;
  cursor: url(../../assets/images/mouseSelect.png) 14 0, pointer !important;
}

.MetaMaskLink { 
  cursor: url(../../assets/images/mouseSelect.png) 14 0, pointer !important;
  border: 2px solid #13a856;
  border-radius: 10px;
  text-decoration: unset !important;
}
.MetaMaskLink:hover { 
  background-color: #1d3645;
  border: 2px solid #c9529b !important;
  box-shadow: 0px 3px 16px 0px #000000;
}
.MetaMaskGif { 
  width: 4rem;
}
.MetaMaskDesc{ 
  padding-right: 1rem !important;
  color: rgb(246, 133, 27);
  font-weight: bolder !important;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;
  font-size: large !important;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.divideR {
  height: 2px;
  background: linear-gradient(to right, #ffffff 7.5px, transparent 7.5px);
  background-size: 15px 100%;
}

.web3modal-modal-container { 
  * {
  outline: none;
  -ms-overflow-style: none !important; 
  scrollbar-width: none !important; 
}

*::-webkit-scrollbar {
	display: none !important;
} 
}
.web3modal-modal-card { 
  background-color: #123b4f !important;
  .web3modal-provider-container { 
    background-color: #123b4f !important;
  }
}

@media only screen and (max-width: 800px) {
  .AcItem {
    max-width: 100%;
    width: 100%;
    padding: 10px;
  }
  .AcItem-Bottom { 
    margin-top: 2rem;
  }

  .gloableIDO {
    background-image: unset;
    background-color: #172131;
  }
  .containIDO { 
    padding-bottom: 0;
  }

  .web3modal-modal-container { 
    padding: 8rem 3rem;
    margin-top: 3rem;
  }
  .web3modal-modal-card { 
    background-color: #172131 !important;
    .web3modal-provider-container { 
      background-color: #172131 !important;
    }
  }

}


@media only screen and (max-width: 550px) { 

  .containIDO {
    padding-left: 1rem;
    padding-right: 1rem;
  }

}