#walletInfo-wrap { 
	.MuiDialog-container { 
		.MuiPaper-root { 
			border-radius: 20px !important;
			overflow: hidden !important;
			border: 3px solid rgb(25, 69, 92);
		}
	}
}

.walletInfo {
	padding: 16px 32px 26px 16px;
	// background-color: #7e7e7e;
	background-color: rgb(5, 42, 51);
	font-family: "Exotc350-Bd-BT-Bold";

	.title {
		height: 54px;
		margin-left: 6px;
	}

	.operations {
		flex-direction: row;
		font-size: 0.8rem;
		justify-content: center;
		align-items: center;
	}
	.operations:hover { 
		cursor: pointer;
	}

}


.box {
	margin-top: 6px;
	height: 156px;
	background-color: #373737;
	border-radius: 10px;

	.box-r {
		margin-right: 30px;

		.walletAddress {
			word-break: break-word;
			padding-bottom: 14px;
			border-bottom: 1px solid #262626;
		}
	}
}

.walletIcon { 
	padding: 1rem;
}

.close { 
	font-family:"Exotc350-Bd-BT-Bold" !important;
	color: #373737 !important;
}

.footer {
	.copyInvite {
		&:hover {
			cursor: pointer;
		}
	}

	.logOutBtn {
		justify-content: center;
		align-items: center;
		&:hover {
			cursor: pointer;
		}
		width: 105px;
		height: 36px;
		background: #373737;
		border-radius: 5px;
	}
}

.logOutIcon { 
	width: 1.5rem;
	height: 1.5rem;
}

@media only screen and (max-width: 1000px) {
	.walletInfo {

		.footer {
			.copyInvite,
			.logOutBtn {
				margin-top: 12px !important;
			}
		}
	}

	.box {
		margin-top: 16px !important;
		height: 206px !important;
		flex-direction: column !important;
		justify-content: center !important;

		.box-r {
			margin-left: 12px !important;
			margin-right: 10px !important;
		}
	}

}